import history from "../history"
export const addToCart = (info = {}) => {
    history.push("/cart")
    return {
        type:"ADD_TO_CART",
        payload:info
    }
}


export const deleteItem = (id) => {

    return {
        type:"DELETE_ITEM",
        payload:id
    }
}

export const addClassToNav = (navClass) => {
    return {
        type:"ADD_CLASS_TO_NAV",
        payload:navClass
    }
}
