import React from "react"
import {Link} from "react-router-dom"
import headerImg from "../../../jsons/images/bg_6.jpg"

import {useIntersection} from "react-use"
import {connect} from "react-redux"
import {addClassToNav} from "../../../actions"
const HeaderImg = (props)  => {
    const {title,section} = props
    const headerImgRef = React.useRef(null)
    const observer = useIntersection(headerImgRef,{
        ref:null,
        rootMargin: '0px',
        threshold: .1
    })
    
    observer && observer.intersectionRatio < .1 ? props.addClassToNav("fixed") : props.addClassToNav("")
    return (
        <div ref={headerImgRef} style={{backgroundImage:`url(${headerImg})`}} className="header-img">
            
                <h6><Link to="/e-commerce-winkel">Home</Link> {section} </h6>
                <h2>{title}</h2>
        </div>
    )
}


export default connect(null,{addClassToNav})(HeaderImg)